import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter);//注意用的是VueRouter

//作为router的routes对象
const routes = [
  {
    path: '/',//根路径，即8080主页
    //有children属性则不能再使用name，因为引入的不止一个文件
    //name: 'index',//引入的name
    component: () => import("../views/index.vue"),//按照需要加载，不再需要在头部import
    children: [
      {
        path: "",
        redirect: '/home',//重定向，主页自动转到根路径下的home路径
      },
      {
        path: "/home",
        name: 'home',
        component: () => import("../views/Home.vue")
      },
      {
        path: "/order",
        name: 'order',
        component: () => import("../views/Order.vue")
      },
      {
        path: "/me",
        name: 'me',
        component: () => import("../views/Me.vue")
      },
      {
        path: "/address",
        name: "address",
        component: () => import("../views/Address.vue")
      },
      {
        path: "/city",
        name: "city",
        component: () => import("../views/City.vue")
      },
    ]
  },
  {
    path: '/login',//根路径index的子路径login
    name: 'login',//引入的name
    component: () => import("../views/Login.vue")
  },
  {
    path: '/search',//根路径index的子路径search
    name: 'search',//引入的name
    component: () => import("../views/Search.vue")
  },
  {
    path: '/shop',//根路径index的子路径shop
    name: 'shop',//引入的name
    redirect: "goods",//重定向，默认自动跳转goods分页
    component: () => import("../views/Shops/Shop.vue"),
    children: [
      {
        path: '/goods',//shop页面的子路径goods
        name: 'goods',//引入的name
        component: () => import("../views/Shops/Goods.vue")
      },
      {
        path: '/comments',//shop页面的子路径comments
        name: 'comments',//引入的name
        component: () => import("../views/Shops/Comments.vue")
      },
      {
        path: '/seller',//shop页面的子路径seller
        name: 'seller',//引入的name
        component: () => import("../views/Shops/Seller.vue")
      },
    ]
  },
  {
    path: '/myAddress',//根路径index的子路径myAddress
    name: 'myAddress',//引入的name
    component: () => import("../views/Orders/MyAddress.vue")
  },
  {
    path: '/addAddress',//根路径index的子路径addAddress
    name: 'addAddress',//引入的name
    component: () => import("../views/Orders/AddAddress.vue")
  },
  {
    path: '/settlement',//根路径index的子路径settlement
    name: 'settlement',//引入的name
    component: () => import("../views/Orders/Settlement.vue")
  },
  {
    path: '/remark',//根路径index的子路径remark
    name: 'remark',//引入的name
    component: () => import("../views/Orders/Remark.vue")
  },
  {
    path: '/pay',//根路径index的子路径pay
    name: 'pay',//引入的name
    component: () => import("../views/Orders/Pay.vue")
  },
  {
    path: '/orderinfo',//根路径index的子路径orderinfo
    name: 'orderinfo',//引入的name
    component: () => import("../views/Orders/OrderInfo.vue")
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  linkActiveClass: "active",//选中之后添加样式，应用在Shop页面的NavBar组件中
  routes
})

//路由守卫-初次访问，锁定登录界面：未登录的话，不能够访问login之外的页面
router.beforeEach((to, from, next) => {
  const isLogin = localStorage.ele_login ? true : false;//本地存储中，登录存在则true
  if (to.path == "/login"){//如果当前路径为login，则正常跳转
    next();
  } else {
    //是否在登录状态下？
    isLogin ? next() : next("./login");
  }
})

export default router;
